import React from 'react';
import {Link} from "react-router-dom";

class DepartmentCard extends React.Component {
    constructor(props) {
        super(props);

        this.state = {spans: 0};

        this.imageRef = React.createRef();
    }

    componentDidMount() {
        this.imageRef.current.addEventListener('load', this.setSpans);
    }

    setSpans = () => {
        const height = this.imageRef.current.clientHeight;

        const spans = Math.ceil(height / 10) + 10;

        this.setState({spans});
    };

    render() {
        const {description, urls, title, id,label,link} = this.props.image;

        return (

            <div className="m-2"
                style={{gridRowEnd: `span ${this.state.spans}`,border: "1px solid rgba(0, 0, 0, 0.125)"}}
            >

                    <div className=" " style={{}}>
                        <img style={{width:"100%"}} ref={this.imageRef} alt={description} src={urls}/>
                    </div>
                    <div className=" mx-3 mt-4 d-flex justify-content-between">
                        <h5 className=" card-title f24 Wblack text-fosfori">{title}</h5>

                        <Link to={`/${link}/${id}`}
                              className="btn btn-outline-primary"
                              style={{border: " 1px solid #0098a0"}}>
                            <span className="text-dark f10">{label}</span>
                            <img src="/images/feather-arrow-left.png" style={{height:"8px",width:"14px"}}  alt="next"/>
                        </Link>

                    </div>

            </div>

        );
    }
}

export default DepartmentCard;
